import React, {useState, useEffect} from 'react'
import RadioOutlinedIcon from '@mui/icons-material/RadioOutlined';
import micBG from "../assets/site_images/micbg.jpg"
import studio1 from "../assets/site_images/studio1.jpg"
import settings from "../data/settings"
import CellTowerOutlinedIcon from '@mui/icons-material/CellTowerOutlined';
const Banner = ()=>{

    return(
        <>
                <div className="banner" style={{backgroundImage:`url(${studio1})`}}>
                      <div className="banner-wrapper">
                            <div className="banner-content">
                                <div className="logo">
                                        <CellTowerOutlinedIcon sx={{fontSize:"10em", color:"white"}}/>
                                </div>
                                <div className="appname">
                                    {
                                        settings ? settings[0].setting_value : "Loading"
                                    }
                                </div>
                                <div className="banner-texts">
                                    <div className="banner-text">
                                    {settings ? settings[2].setting_value+" Fm" : "Loading Data"}
                                    </div>
                                </div>
                            </div>
                      </div>
                </div>
        </>
    )
}
export default Banner;