import React from 'react'
import BottomComponent from "../components/bottom_component"
import Appbar from "../components/appbar"
import settings from "../data/settings"
export default function About() {

 
  return (
      <div className="scaffold">
            <Appbar/>
            <div className="main-content">
                  <div className="logo">
                       <img src="https://channel5media.com/assets/logo/logo%20png.png" alt="" />  
                  </div>
                  <div className="summery">
              
                      <div className="title">
                           {
                               settings ? settings[0].setting_value : ""    
                           }
                      </div>
                      <div className="frequency">
                             {
                               settings ? settings[2].setting_value : ""    
                           }
                      </div>
                      <div className="slogan">
                             {
                                 settings ? settings[3].setting_value : ""    
                           }
                      </div>
                  </div>
                 <p>
                     {settings ? settings[1].setting_value: "" }
                 </p>
           
              
                 
            </div>
            <BottomComponent/>
      </div>
  )
}
