import React, {useState, useEffect} from 'react'
import Appbar from '../components/appbar'
import BottomComponent from '../components/bottom_component'
import {Card, CardHeader, CardContent, Typography, Avatar, CardMedia, CardActions, LinearProgress, IconButton} from '@mui/material'
import axios from 'axios'
export default function News() {
  const [news, setNews] = useState()

  const fetchNews = async()=>{
      const response = await axios.get("https://newsapi.org/v2/top-headlines?country=us&apiKey=b1a6c677bedb4471adb050e872224a58")
      const result = response.data.articles
      console.log(response)
      setNews(result)
  }

  useEffect(()=>{
   fetchNews()
  })
  return (
     <div className="scaffold">
          <Appbar/>
          <div className="main-content">
                  {
                     /*
                           <div className="content-status">
                       <div className="icon">
                          <span className="material-symbols-outlined">newspaper</span>
                       </div>
                       <div className="label">
                          Oops !! We're Fixing this
                       </div>
                  </div>
                     */
                    
                  }
                  <div className="articles">
                        {
                           news ? news.map(data=>{
                              return <Card sx={{backgroundColor:"rgb(23, 26, 34)", color:"white", mt:2, mb:2}}>
                              <CardHeader title={data.title} sx={{color:"orange"}} avatar={<Avatar />}/>
                              <CardMedia component="img" image={data.urlToImage}></CardMedia>
                              <CardContent>
                                   <Typography>{data.description}</Typography>
                              </CardContent>
                          </Card>
                           })
                           : <LinearProgress color="warning"></LinearProgress>
                        }
                  </div>
          </div>
          <BottomComponent/>
     </div>
  )
}
