import Nav_data from "../data/nav_data"
const BottomNav = ()=>{
    return(
        <>
                    <div className="bottom-nav">
                         {
                            Nav_data.map(data=>{
                                return(
                                <a href={data.path} className="nav-item">
                                <span className="material-symbols-outlined">{data.icon}</span>
                                <span>{data.label}</span>
                                </a>
                                )
                            })
                         }
                    </div>
        </>
    )
}
export default BottomNav