import React, { useState, useEffect, useRef } from 'react';
import settings from "../data/settings"
import PlayCircleFilledWhiteOutlinedIcon from '@mui/icons-material/PlayCircleFilledWhiteOutlined';
import PauseCircleOutlinedIcon from '@mui/icons-material/PauseCircleOutlined';
import VolumeUpOutlinedIcon from '@mui/icons-material/VolumeUpOutlined';
import VolumeDownOutlinedIcon from '@mui/icons-material/VolumeDownOutlined';
import ReactPlayer from 'react-player'
import {IconButton} from '@mui/material'
import {Howl} from 'howler'
const Player = () => {
  const [playing, setPlaying] = useState(false)
  
  console.log(settings[4].setting_value)

  const hwl = new Howl({
    src:[settings[4].setting_value],
    html5:true,
  })

  
  const playAudio = () => {
    setPlaying(true);
    hwl.play()
  }


  const pauseAudio = () => {
    setPlaying(false);
     hwl.pause()
  }
 
  const volumeReduce = () => {
      hwl.volume(0.5)
  }
  const volumeIncrease = () => {
      hwl.volume(1)
  }
  
  useEffect(()=>{
      //volumeIncrease()
      //volumeReduce()
  })


  return (
    <>
      <div className="player-area">
        <div className="controls">
          <IconButton onClick={volumeReduce} id="volume_down">
               <VolumeDownOutlinedIcon sx={{fontSize:"2em", color:"orange"}}/>
          </IconButton>
          <IconButton id="play_pause" onClick={playing ? pauseAudio : playAudio}>
            {playing ? 
              
                 <PauseCircleOutlinedIcon sx={{fontSize:"3.5em", color:"orange"}}/>
              
             : 
                <PlayCircleFilledWhiteOutlinedIcon sx={{fontSize:"3.5em", color:"orange"}}/>
            }
          </IconButton>
          <IconButton onClick={volumeIncrease} id="volume_up">
               <VolumeUpOutlinedIcon sx={{fontSize:"2em", color:"orange"}}/>
          </IconButton>
        </div>
      </div>
           
    </>
  );
};

export default Player;
